.video {
  min-height: 100%;
  min-width: 100%;
  position: absolute;
  bottom: auto;
  right: auto;
  top: -10%;
  z-index: -1;
  /* background-color: rgba(0,0,0,0.6); */

}

.videoWrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: rgba(0,0,0,0.6);
    /* z-index: -1; */

}
